*, html, body {
  font-family: 'Roboto', sans-serif;
  box-sizing: border-box; }

header {
  background: #0c002b;
  color: white;
  overflow: auto;
  padding: 20px 15px; }
  header .logo {
    float: left; }
  header nav {
    float: right; }
    header nav a {
      color: white;
      text-decoration: none;
      padding: 10px 20px;
      text-transform: capitalize;
      font-weight: 300;
      font-size: 0.9rem; }
      header nav a:hover {
        color: #ff718e; }
      header nav a.register-btn {
        background: #FF718E;
        padding: 7px 25px;
        text-align: center;
        color: white;
        border-radius: 5px; }

#content-area {
  overflow: auto;
  width: 100%;
  min-height: 100vh; }

#filter {
  background: #FAF7F5;
  width: 30%;
  max-width: 300px;
  display: inline-block;
  float: left; }
  #filter .inside {
    padding: 20px;
    max-width: 300px; }
  #filter h4 {
    font-size: 0.9rem;
    margin-bottom: 25px;
    font-weight: 500px;
    text-transform: uppercase; }
  #filter select {
    background: transparent;
    padding: 10px;
    font-size: 14px;
    font-weight: 300;
    color: #1b1b1b;
    display: block;
    margin-bottom: 25px;
    width: 100%; }
  #filter .filters .title {
    font-size: 0.9rem;
    display: block;
    margin-bottom: 1rem;
    color: #828D9D; }
  #filter .filters input[type="text"] {
    border-radius: 5px;
    font-size: 1rem;
    padding: 0.4rem;
    width: 45%;
    display: inline-block;
    margin-right: 2.5%;
    margin-bottom: 1rem; }
  #filter .filters.extras label {
    width: 100%;
    display: block;
    padding: 10px 0; }
    #filter .filters.extras label input[type="checkbox"] {
      float: right; }

#listings {
  width: 70%;
  float: left;
  display: inline-block;
  padding: 15px;
  background: #FFFEFF; }
  #listings .search-area input[type="text"] {
    width: 70%;
    background: transparent;
    font-size: 2rem;
    border: none;
    border-bottom: 1px solid #828D9D;
    margin-bottom: 20px;
    font-weight: 100;
    color: #DADDE2; }
  #listings .sortby-area {
    margin-bottom: 20px; }
    #listings .sortby-area .results {
      color: #828D9D;
      font-size: 0.8rem;
      display: inline-block; }
    #listings .sortby-area .sort-options {
      float: right; }
      #listings .sortby-area .sort-options select {
        font-size: 0.9rem;
        font-weight: 300;
        border: 1px solid #DADDE2;
        background: transparent;
        padding: 5px;
        display: inline-block;
        margin-right: 15px;
        vertical-align: top; }
      #listings .sortby-area .sort-options .view {
        display: inline-block; }
        #listings .sortby-area .sort-options .view .fas {
          margin-right: 15px;
          font-size: 1.5rem; }
  #listings .listings-results {
    margin-bottom: 60px; }
    #listings .listings-results .listing {
      background: #fff;
      border-radius: 5px;
      box-shadow: 0px 9px 54px -4px #e0e0e0; }
    #listings .listings-results .listing-img {
      color: white;
      padding: 15px;
      position: relative;
      background: url("https://freshome.com/wp-content/uploads/2014/04/Gorgeous-evening-view-of-Russet-Residence-.jpg") no-repeat center center;
      background-size: cover;
      min-height: 200px;
      -webkit-border-top-left-radius: 5px;
      -webkit-border-top-right-radius: 5px;
      -moz-border-radius-topleft: 5px;
      -moz-border-radius-topright: 5px;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px; }
      #listings .listings-results .listing-img:hover .details {
        opacity: 1; }
      #listings .listings-results .listing-img:hover .address {
        opacity: 0; }
      #listings .listings-results .listing-img .address {
        font-size: 1.3rem;
        text-transform: capitalize;
        position: absolute;
        bottom: 20px;
        opacity: 1;
        -webkit-transition: all 0.4s ease-in-out;
        transition: all 0.4s ease-in-out;
        text-shadow: 0px 1px 4px #5b5b5b; }
      #listings .listings-results .listing-img .details {
        -webkit-transition: all 0.4s ease-in-out;
        transition: all 0.4s ease-in-out;
        opacity: 0;
        padding: 30px;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        -webkit-border-top-left-radius: 5px;
        -webkit-border-top-right-radius: 5px;
        -moz-border-radius-topleft: 5px;
        -moz-border-radius-topright: 5px;
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
        /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#4e02e5+0,901ced+100 */
        background: rgba(78, 2, 229, 0.8);
        /* Old browsers */
        /* FF3.6-15 */
        /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(135deg, rgba(78, 2, 229, 0.8) 0%, rgba(144, 28, 237, 0.8) 100%);
        /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#4e02e5', endColorstr='#901ced',GradientType=1 );
        /* IE6-9 fallback on horizontal gradient */ }
        #listings .listings-results .listing-img .details .user-img {
          width: 35px;
          height: 35px;
          border-radius: 50%;
          border: 2px solid white;
          display: inline-block;
          background: url("https://freshome.com/wp-content/uploads/2014/04/Gorgeous-evening-view-of-Russet-Residence-.jpg") no-repeat center center;
          background-size: cover; }
        #listings .listings-results .listing-img .details .user-details {
          margin-left: 15px;
          display: inline-block;
          vertical-align: top; }
          #listings .listings-results .listing-img .details .user-details .user-name {
            font-weight: 300;
            font-size: 0.9rem;
            display: block;
            color: white;
            margin-top: 3px;
            margin-bottom: 5px; }
          #listings .listings-results .listing-img .details .user-details .post-date {
            font-size: 0.9rem;
            display: block;
            color: rgba(255, 255, 255, 0.7); }
        #listings .listings-results .listing-img .details .listing-details {
          margin-top: 20px;
          margin-bottom: 10px; }
          #listings .listings-results .listing-img .details .listing-details .far,
          #listings .listings-results .listing-img .details .listing-details .fas {
            display: inline-block;
            font-size: 20px;
            margin-right: 15px; }
          #listings .listings-results .listing-img .details .listing-details span {
            font-size: 0.8rem;
            font-weight: 300;
            display: inline-block;
            vertical-align: top;
            position: relative;
            top: 3px; }
          #listings .listings-results .listing-img .details .listing-details .floor-space {
            margin-bottom: 10px; }
    #listings .listings-results .bottom-info {
      padding: 20px 15px; }
      #listings .listings-results .bottom-info .price {
        font-size: 1rem;
        display: block;
        margin-bottom: 10px;
        color: #828D9D; }
      #listings .listings-results .bottom-info .location {
        font-size: 1.2rem;
        display: block;
        color: #DADDE2; }

.view-btn {
  background: black;
  padding: 8px;
  font-size: 0.9rem;
  border-radius: 5px;
  text-align: center;
  max-width: 150px;
  -webkit-transition: background 0.3s ease-in-out;
  transition: background 0.3s ease-in-out; }
  .view-btn:hover {
    background: #FF718E;
    cursor: pointer; }

#pagination {
  width: 100%;
  display: block;
  text-align: center; }
  #pagination .pages {
    margin: 0 auto;
    display: inline-block;
    background: white;
    box-shadow: 0px 9px 54px -4px #e0e0e0; }
    #pagination .pages li {
      display: inline-block;
      padding: 20px 30px; }
      #pagination .pages li:hover, #pagination .pages li.active {
        color: white;
        font-weight: 700;
        background: #FF718E;
        cursor: pointer; }

